.App {
  text-align: center;
  top: 100px;
  display: block !important;
  bottom: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.bg1 {
  background: linear-gradient(
    180deg,
    rgba(249, 188, 186, 1) 30%,
    rgba(192, 230, 224, 1) 100%
  );
}
.bg2 {
  background: linear-gradient(
    180deg,
    rgba(249, 188, 186, 1) 90%,
    rgba(192, 230, 224, 1) 100%
  );
}

.bg3 {
  background: linear-gradient(
    180deg,
    rgba(249, 188, 186, 1) 30%,
    rgba(192, 230, 224, 1) 100%
  );
}

.bg4 {
  background: linear-gradient(
    180deg,
    rgba(249, 188, 186, 1) -250%,
    rgba(192, 230, 224, 1) 100%
  );
}
